@use 'constants/colors' as colors;
@use 'constants/functions' as functions;

.content {
  padding: functions.rhythm(4);
  background-color: colors.$lightest;
  overflow: hidden;
}

.section {
  margin-bottom: functions.rhythm(2);
  &:last-child {
    margin-bottom: 0;
  }
  h2, h3 {
    margin-bottom: functions.rhythm(1);
  }
  p {
    margin: functions.rhythm(1) 0;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.wrapper {
  @media screen and (min-width: 992px) {
    .heading, .content, .footer {
      padding-left: 10%;
      padding-right: 10%;
    }
  }

  @media screen and (min-width: 1281px) {
    .heading, .content, .footer {
      padding-left: 25%;
      padding-right: 25%;
    }
  }
}
