@use 'constants/colors' as colors;
@use 'constants/functions' as functions;

.footer {
  padding: functions.rhythm(1) functions.rhythm(4);
  background-color: colors.$dark;
  color: colors.$lightest;
  display: flex;
  align-items: center;

  .icons {
    list-style: none;
    padding: 0;
    margin-left: functions.rhythm(2);
    font-size: 1.4em;

    .icon {
      display: inline-block;

      .icon-link {
        color: colors.$lightest;
        &:hover {
          color: gray;
        }
      }
    }
  }
}
