@use 'constants/colors' as colors;
@use 'constants/functions' as functions;

#disqus_thread {
  background-color: colors.$secondary-light;
  padding: functions.rhythm(4);
}

.compare-table {
  overflow-x: auto;
  white-space: nowrap;

  table {
    width: 100%;
    td {
      background-color: #151515;
      padding: 0;
      width: 50%;
      vertical-align: middle;
      border-right: 5px solid white;
      border-left: 5px solid white;
    }
    td:first-child {
      border-left: 0;
    }
    td:last-child {
      border-right: 0;
    }
  }
}

.answers {
  list-style-type: none;
  padding: 0;
  margin-top: functions.rhythm(1);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.answer {
  display: flex;
  align-items: center;
  margin-right: functions.rhythm(2);
}

.answer-letter {
  background-color: colors.$dark;
  color: colors.$lightest;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  min-width: 50px;
  height: 50px;
  min-height: 50px;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  border-radius: 100px;
  margin-right: functions.rhythm(1);
}

#answer {
  display: none;
  flex-direction: column;
}

.comment {
  background-color: colors.$secondary-light;
  padding: functions.rhythm(2);
  margin: functions.rhythm(1) 0;
}

.post-wrapper {
  .header {
    padding: functions.rhythm(1) functions.rhythm(4);

    .site-name {
      font-size: 1.5em;
      font-weight: normal;

      a {
        color: colors.$lightest;
        text-decoration: none;
      }
    }
  }

  @media screen and (min-width: 992px) {
    .header, .content, .footer, #disqus_thread {
      padding-left: 10%;
      padding-right: 10%;
    }
  }

  @media screen and (min-width: 1281px) {
    .header, .content, .footer, #disqus_thread {
      padding-left: 25%;
      padding-right: 25%;
    }
  }

  .section {
    margin-bottom: rhythm(4);

    h1 {
      font-size: 1.8em;
    }
  }

  .post__date {
    font-style: italic;
  }
}
