@use 'constants/colors' as colors;
@use 'constants/functions' as functions;

.header {
  background-color: colors.$dark;
  color: colors.$lightest;
}

.heading {
  padding: functions.rhythm(1.5) functions.rhythm(4);
  h1 {
    font-size: 1.5em;
    margin: functions.rhythm(1) 0;
  }
  h2 {
    font-size: 1em;
    font-weight: normal;
    margin: functions.rhythm(1) 0;
  }
}

