@use 'constants/colors' as colors;
@use 'constants/functions' as functions;

.navigation {
  text-align: left;
  border-top: 1px solid colors.$light;

  ul {
    display: flex;
    flex-direction: row;
    justify-content: left;
    list-style-type: none;
    padding-left: functions.rhythm(4);

    li {
      position: relative;
      height: 100%;
      margin-right: functions.rhythm(4);

      a {
        display: block;
        padding: functions.rhythm(1.5) 0;
        color: colors.$lightest;
        transition: all 0.3s;
        text-decoration: none;

        &:after {
          transition: height .2s ease-in-out 0s;
          position: absolute;
          right: 0;
          bottom: 0;
          left: 0;
          display: block;
          height: 0;
          content: "";
          background-color: colors.$light;
        }
      }

      a.active, a:hover {
        &:after {
          height: 3px;
        }
      }
    }
  }
}

@media screen and (min-width: 992px) {
  .navigation {
    padding-left: 10%;
    padding-right: 10%;

    ul {
      padding-left: 0;
    }
  }
}

@media screen and (min-width: 1281px) {
  .navigation {
    padding-left: 25%;
    padding-right: 25%;

    ul {
      padding-left: 0;
    }
  }
}
