@use 'constants/colors' as colors;
@use 'constants/fonts' as fonts;
@use 'constants/functions' as functions;

* {
  line-height: 1.6;
}

:root {
  font-size: 16px;
}

body {
  background-color: colors.$dark;
  font-family: fonts.$sans-serif;
}

a:link {
  color: colors.$dark;
}

a:visited {
  color: colors.$link-visited;
}

.content a:hover {
  opacity: 0.75;
}

h1, h2, h3 {
  font-weight: bold;
}

h1, h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.1em;
}

ul {
  list-style-type: disc;
  padding-left: functions.rhythm(3);
  line-height: normal;
}

strong {
  font-weight: bold;
}

em {
  font-style: italic;
}

th {
  font-weight: bold;
}

th, td {
  padding: functions.rhythm(1);
}

tbody tr:nth-child(odd) {
  background-color: colors.$secondary-light;
}

dt {
  background-color: colors.$secondary-light;
  padding: functions.rhythm(1);
}

dd {
  padding: functions.rhythm(1) functions.rhythm(2);
}

hr {
  border: 0;
  height: 1px;
  background: #999;
  margin: functions.rhythm(4) 0;
}
